.card {
  background: #fff;
  border-radius: 2px;
  height: 200px;
  margin: 1rem;
  position: relative;
  width: 200px;
  /* box-shadow: 0 3px 6px rgb(0, 0, 0), 0 3px 6px rgb(0, 0, 0); */
  text-align: left;
  cursor: pointer;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
}

.card > .img-container > img {
  width: 100%
}
.wrapper {
  padding-top: 20px;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 20px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
  background-image: url(/blackadder-clicky-game/assets/images/countryside.jpg);
}
.title {
  width: 100%;
  text-align: center;
  font-weight: 700;
  font-family: 'Charm', cursive;
  padding: 10px !important;
  cursor: default !important;
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

